* {
  box-sizing: border-box;
  margin: 0;
  padding: 0;
  font-family: "Lucida Sans", "Lucida Sans Regular", "Lucida Grande",
    "Lucida Sans Unicode", Geneva, Verdana, sans-serif;
  transition: all ease-in 0.4s;
}
.row-title {
  margin-left: 20px;
  margin-top: 10px;
  font-weight: 900;
  font-size: 20px;
}
.row-container {
  display: flex;
  gap: 15px;
  margin: 20px;
  margin-bottom: 50px;
  overflow-x: scroll;
}
.row-container::-webkit-scrollbar {
  display: none;
}

.movie-poster {
  max-height: 12vh;
  object-fit: contain;
}
.movie-poster:hover {
  transform: scale(1.08);
}

.movie-poster-large {
  max-height: 25vh;
}
